import PlayerSettings from "./PlayerSettings";

/**
 * The reducer for the state of an App component.
 */
export default function appReducer(state: AppState, action: AppAction): AppState {
  switch (action.type) {
    case 'blackSettingsChange':
      return {
        ...state,
        blackSettings: action.settings,
      };
    case 'whiteSettingsChange':
      return {
        ...state,
        whiteSettings: action.settings,
      };
    case 'startEndButtonClick':
      if (state.inAnimation) {
        return {
          ...state,
          startEndButtonClicked: true,
        };
      }
      return {
        ...state,
        inGame: !state.inGame,
        startEndButtonClicked: false,
      };
    case 'animationStart':
      return {
        ...state,
        inAnimation: true,
      };
    case 'animationEnd':
      return {
        ...state,
        inGame: state.startEndButtonClicked ? !state.inGame : state.inGame,
        startEndButtonClicked: false,
        inAnimation: false,
      };
    default:
      console.error('Invalid action type:', action);
      return state;
  }
}

/**
 * The state of an App component.
 */
export type AppState = {
  inGame: boolean,
  startEndButtonClicked: boolean,
  inAnimation: boolean,
  blackSettings: PlayerSettings,
  whiteSettings: PlayerSettings,
};

/**
 * An action that can be dispatched to the App reducer.
 */
export type AppAction = (
  { type: 'blackSettingsChange', settings: PlayerSettings } |
  { type: 'whiteSettingsChange', settings: PlayerSettings } |
  { type: 'startEndButtonClick' } |
  { type: 'animationStart' } |
  { type: 'animationEnd' }
);
