import { GameState, PlayerAction } from 'othello-rs';

import { getSquareStatesFromGameState } from './GameState';
import SquareState from './SquareState';

/**
 * The reducer for the state of a GameView component.
 */
export default function gameViewReducer(
  state: GameViewState,
  action: GameViewAction,
): GameViewState {
  switch (action.type) {
    case 'gameStart':
      if (state.gameState) {
        return state;
      }
      return {
        gameState: GameState.initial_state(),
        previousSquareStates: getSquareStatesFromGameState(state.gameState),
        pendingPlayerActionId: null,
        playerAction: null,
      };
    case 'gameEnd':
      if (!state.gameState) {
        return state;
      }
      return {
        gameState: null,
        previousSquareStates: getSquareStatesFromGameState(state.gameState),
        pendingPlayerActionId: null,
        playerAction: null,
      };
    case 'animationEnd':
      return {
        ...state,
        previousSquareStates: null,
      };
    case 'playerActionStart':
      if (!state.gameState) {
        return state;
      }
      return {
        ...state,
        pendingPlayerActionId: action.playerActionId,
        playerAction: null,
      };
    case 'playerActionReady':
      if (action.playerActionId !== state.pendingPlayerActionId) {
        return state;
      }
      return {
        ...state,
        pendingPlayerActionId: null,
        playerAction: action.playerAction,
      };
    case 'playerActionApply':
      // Do nothing if the player action is unavailable or an animation is in progress.
      if (!state.gameState || !state.playerAction || state.previousSquareStates) {
        return state;
      }
      return {
        gameState: state.gameState.apply_action(state.playerAction),
        previousSquareStates: getSquareStatesFromGameState(state.gameState),
        pendingPlayerActionId: null,
        playerAction: null,
      };
    default:
      console.error('Invalid action type:', action);
      return state;
  }
}

/**
 * The state of a GameView component.
 */
export type GameViewState = {
  gameState: GameState | null,
  previousSquareStates: SquareState[][] | null,
  pendingPlayerActionId: number | null,
  playerAction: PlayerAction | null,
};

/**
 * An action that can be dispatched to the GameView reducer.
 */
export type GameViewAction = (
  { type: 'gameStart' } |
  { type: 'gameEnd' } |
  { type: 'animationEnd' } |
  { type: 'playerActionStart', playerActionId: number } |
  { type: 'playerActionReady', playerActionId: number, playerAction: PlayerAction } |
  { type: 'playerActionApply' }
);
