import { Label, Select, SelectOnChangeData, makeStyles, useId } from "@fluentui/react-components";

import PlayerSettings from "./PlayerSettings";

/**
 * A component for picking a PlayerSettings object.
 */
export default function PlayerPicker({
  label,
  disabled,
  playerSettings,
  onChange,
}: PlayerPickerProps) {
  const styles = useStyles();
  const playerTypeSelectId = useId();

  function handlePlayerTypeChange(
    _event: React.ChangeEvent<HTMLSelectElement>,
    data: SelectOnChangeData,
  ) {
    switch (PLAYER_TYPE_VALUES[PLAYER_TYPE_TEXTS.indexOf(data.value)]) {
      case 'human':
        onChange({ type: 'human' });
        break;
      case 'random':
        onChange({ type: 'random' });
        break;
      case 'negaScout':
        onChange({ type: 'negaScout', depth: 10 });
        break;
      default:
        console.error('Invalid player type:', data.value);
    }
  }

  return (
    <div className={styles.root}>
      <Label htmlFor={playerTypeSelectId}>{`${label}:`}</Label>
      <Select
        id={playerTypeSelectId}
        disabled={disabled}
        value={PLAYER_TYPE_TEXTS[PLAYER_TYPE_VALUES.indexOf(playerSettings.type)]}
        onChange={handlePlayerTypeChange}
      >
        {PLAYER_TYPE_TEXTS.map((text) => (
          <option key={text}>{text}</option>
        ))}
      </Select>
    </div>
  );
}

/**
 * Properties of the PlayerPicker component.
 */
export type PlayerPickerProps = {
  label: string;
  disabled: boolean;
  playerSettings: PlayerSettings;
  onChange: (playerSettings: PlayerSettings) => void;
};

const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'start',
  },
});

const PLAYER_TYPE_VALUES = ['human', 'random', 'negaScout'];
const PLAYER_TYPE_TEXTS = ['Human', 'Random', 'NegaScout'];
