import BoardConstants from './BoardConstants';
import SquareState, { isDiscState, isLegalMoveState } from './SquareState';

/**
 * Returns the visual properties of the given square state.
 * @param state - A square state.
 * @param counterpart - If `state` is the beginning of a transition, `counterpart` is the end of
 * that transition. If `state` is the end of a transition, `counterpart` is the beginning of that
 * transition.
 */
export default function getSquareProps(state: SquareState, counterpart: SquareState) {
  const props: SquareProps = {
    size: 0,
    cornerRadius: 0,
    opacities: {
      shadow: 0,
      blackLegalMove: 0,
      whiteLegalMove: 0,
      blackDisc: 0,
      whiteDisc: 0,
    },
  };
  if (isLegalMoveState(state)) {
    props.size = BoardConstants.LEGAL_MOVE_INDICATOR_SIZE;
    props.opacities[state === SquareState.BlackLegalMove ? 'blackLegalMove' : 'whiteLegalMove'] =
      BoardConstants.LEGAL_MOVE_INDICATOR_OPACITY;
  } else if (isDiscState(state)) {
    props.size = BoardConstants.DISC_RADIUS * 2;
    props.cornerRadius = BoardConstants.DISC_RADIUS;
    props.opacities.shadow = 1;
    props.opacities[state === SquareState.BlackDisc ? 'blackDisc' : 'whiteDisc'] = 1;
  } else if (counterpart !== SquareState.Empty) {
    const counterpartProps = getSquareProps(counterpart, state);
    props.size = counterpartProps.size / 2;
    props.cornerRadius = counterpartProps.cornerRadius / 2;
  }
  return props;
}

/**
 * The visual properties of a square.
 */
export type SquareProps = {
  size: number;
  cornerRadius: number;
  opacities: {
    shadow: number;
    blackLegalMove: number;
    whiteLegalMove: number;
    blackDisc: number;
    whiteDisc: number;
  },
};
