import React, { forwardRef, memo, useMemo } from 'react';

import BoardConstants from './BoardConstants';
import SquareState, { areSquareStatesEqual } from './SquareState';
import createSquareDef from './createSquareDef';

import './StatelessBoard.css';

/**
 * A stateless component that renders a board transitioning between two states.
 */
const StatelessBoard = memo(forwardRef(function StatelessBoard(
  props: StatelessBoardProps,
  ref: React.ForwardedRef<SVGSVGElement>,
) {
  const viewBoxXy = -BoardConstants.MARGIN_WIDTH;
  const viewBoxSize = 8 * BoardConstants.SQUARE_SIZE + 2 * BoardConstants.MARGIN_WIDTH;
  return (
    <div className='board-svg-container'>
      <svg
        ref={ref}
        className='board-svg'
        viewBox={`${viewBoxXy} ${viewBoxXy} ${viewBoxSize} ${viewBoxSize}`}
        onClick={props.onClick}
      >
        {useMemo(() => (
          <>
            {createDefs()}
            {createBorder()}
            {createBackground()}
            {createGrid()}
            {createDots()}
          </>
        ), [])}
        {createSquareUses(props.fromSquareStates, props.toSquareStates)}
      </svg>
    </div>
  );
}), arePropsEqual);

export default StatelessBoard;

/**
 * Properties of the StatelessBoard component.
 */
export type StatelessBoardProps = {
  fromSquareStates: SquareState[][],
  toSquareStates: SquareState[][],
  onClick: (event: React.MouseEvent<SVGSVGElement>) => void,
};

/**
 * Returns whether the two sets of properties are equal.
 */
function arePropsEqual(prevProps: StatelessBoardProps, nextProps: StatelessBoardProps) {
  return (
    areSquareStatesEqual(prevProps.fromSquareStates, nextProps.fromSquareStates) &&
    areSquareStatesEqual(prevProps.toSquareStates, nextProps.toSquareStates) &&
    prevProps.onClick === nextProps.onClick
  );
}

/**
 * Returns the ID of the element that defines a square transitioning between the given states.
 */
function getSquareDefId(fromState: SquareState, toState: SquareState) {
  let id = `square-state-${SquareState[fromState]}`;
  if (toState !== fromState) {
    id += `-to-${SquareState[toState]}`;
  }
  return id;
}

/**
 * Creates the `<defs>` element.
 */
function createDefs() {
  const squareStates: SquareState[] =
    Object.keys(SquareState).map(Number).filter((state) => !isNaN(state));
  const squareDefs = [];
  for (const fromState of squareStates) {
    for (const toState of squareStates) {
      squareDefs.push(createSquareDef(getSquareDefId(fromState, toState), fromState, toState));
    }
  }
  return (
    <defs>
      <filter id={BoardConstants.BORDER_SHADOW_FILTER_ID}>
        <feDropShadow dx='0' dy='0' stdDeviation='0.7' />
      </filter>
      <filter id={BoardConstants.DISC_SHADOW_FILTER_ID}>
        <feDropShadow dx='0' dy='0' stdDeviation='0.3' />
      </filter>
      <radialGradient id={BoardConstants.BLACK_DISC_GRADIENT_ID}>
        <stop offset='90%' stopColor='rgb(24, 24, 24)' />
        <stop offset='100%' stopColor='rgb(0, 0, 0)' />
      </radialGradient>
      <radialGradient id={BoardConstants.WHITE_DISC_GRADIENT_ID}>
        <stop offset='90%' stopColor='rgb(255, 255, 255)' />
        <stop offset='100%' stopColor='rgb(192, 192, 192)' />
      </radialGradient>
      {squareDefs}
    </defs>
  );
}

/**
 * Creates the border and its shadow.
 */
function createBorder() {
  const xy = -BoardConstants.BORDER_WIDTH;
  const size = 8 * BoardConstants.SQUARE_SIZE + 2 * BoardConstants.BORDER_WIDTH;
  const radius = BoardConstants.BORDER_WIDTH;
  const commonProps = { x: xy, y: xy, width: size, height: size, rx: radius, ry: radius };
  return (
    <>
      <rect {...commonProps} filter={`url(#${BoardConstants.BORDER_SHADOW_FILTER_ID})`} />
      <rect {...commonProps} />
    </>
  );
}

/**
 * Creates the background of the board.
 */
function createBackground() {
  const size = 8 * BoardConstants.SQUARE_SIZE;
  return (
    <rect width={size} height={size} fill='rgb(63, 142, 106)' />
  );
}

/**
 * Creates the grid lines.
 */
function createGrid() {
  const size = 8 * BoardConstants.SQUARE_SIZE;
  const lines = [];
  for (let i = 0; i < 9; ++i) {
    const a = i * BoardConstants.SQUARE_SIZE;
    lines.push(
      <line key={`row-${i}`} x1={0} y1={a} x2={size} y2={a} />
    );
    lines.push(
      <line key={`column-${i}`} x1={a} y1={0} x2={a} y2={size} />
    );
  }
  return (
    <g stroke='black' strokeWidth={BoardConstants.GRID_LINE_WIDTH}>
      {lines}
    </g>
  );
}

/**
 * Creates the dots at grid intersections.
 */
function createDots() {
  const dots = [];
  for (const row of [2, 6]) {
    const cy = row * BoardConstants.SQUARE_SIZE;
    for (const column of [2, 6]) {
      const cx = column * BoardConstants.SQUARE_SIZE;
      dots.push(
        <circle key={`${row}-${column}`} cx={cx} cy={cy} r={BoardConstants.DOT_RADIUS} />
      );
    }
  }
  return (
    <>{dots}</>
  );
}

/**
 * Creates the squares on the board.
 */
function createSquareUses(fromStates: SquareState[][], toStates: SquareState[][]) {
  const uses = [];
  for (let row = 0; row < 8; ++row) {
    const cy = (row + 0.5) * BoardConstants.SQUARE_SIZE;
    for (let column = 0; column < 8; ++column) {
      const cx = (column + 0.5) * BoardConstants.SQUARE_SIZE;
      uses.push(
        <use
          key={`${row}-${column}`}
          href={`#${getSquareDefId(fromStates[row][column], toStates[row][column])}`}
          transform={`translate(${cx}, ${cy})`}
        />
      );
    }
  }
  return (
    <>{uses}</>
  );
}
