import {
  Link,
  Toast,
  ToastBody,
  ToastTitle,
  ToastTrigger,
  Toaster,
  useId,
  useToastController,
} from '@fluentui/react-components';
import React, { useCallback, useEffect, useReducer, useRef } from 'react';

import { GameState, Player, PlayerAction, player_opponent } from 'othello-rs';

import { AgentWorkerReadyMessage, AgentWorkerStartMessage } from './AgentWorkerMessage';
import Board from './Board';
import { getLegalMoveFlatIndices, getSquareStatesFromGameState, isEndgame } from './GameState';
import PlayerSettings from './PlayerSettings';
import gameViewReducer from './gameViewReducer';

/**
 * A component that runs games by taking turns between players and stepping the game state forward.
 */
export default function GameView({
  blackSettings,
  whiteSettings,
  agentWorker,
  inGame,
  onGameEnd,
  onAnimationStart,
  onAnimationEnd,
}: GameViewProps) {
  const toasterId = useId();
  const { dispatchToast } = useToastController(toasterId);

  const dispatchPlayerPassToast = useCallback((content: React.ReactNode) => {
    dispatchToast(content, { timeout: 5000, intent: 'info' });
  }, [dispatchToast]);

  const resolveClickPromise = useRef<ResolveClickPromise | null>(null);
  const resolveAgentPromises = useRef<ResolveAgentPromises>({});

  const blackSettingsString = JSON.stringify(blackSettings);
  const blackPlayer = useCallback((gameState: GameState, playerActionId: number) => {
    return getPlayerActionPromise(
      gameState,
      JSON.parse(blackSettingsString) as PlayerSettings,
      playerActionId,
      agentWorker,
      dispatchPlayerPassToast,
      resolveClickPromise,
      resolveAgentPromises,
    );
  }, [agentWorker, blackSettingsString, dispatchPlayerPassToast]);

  const whiteSettingsString = JSON.stringify(whiteSettings);
  const whitePlayer = useCallback((gameState: GameState, playerActionId: number) => {
    return getPlayerActionPromise(
      gameState,
      JSON.parse(whiteSettingsString) as PlayerSettings,
      playerActionId,
      agentWorker,
      dispatchPlayerPassToast,
      resolveClickPromise,
      resolveAgentPromises,
    );
  }, [agentWorker, whiteSettingsString, dispatchPlayerPassToast]);

  const handleMessage = useCallback((event: MessageEvent) => {
    const message = event.data as AgentWorkerReadyMessage;
    if (message.type !== 'ready') {
      console.error('Unexpected message type:', message.type);
      return;
    }
    const resolvePromise = resolveAgentPromises.current[message.playerActionId];
    if (!resolvePromise) {
      return;
    }
    resolvePromise(PlayerAction.from_js_value(JSON.parse(message.playerActionString)));
    delete resolveAgentPromises.current[message.playerActionId];
  }, []);

  useEffect(() => {
    agentWorker.addEventListener('message', handleMessage);
    return () => {
      agentWorker.removeEventListener('message', handleMessage);
    };
  }, [agentWorker, handleMessage]);

  const [gameViewState, gameViewDispatch] = useReducer(gameViewReducer, {
    gameState: null,
    previousSquareStates: null,
    pendingPlayerActionId: null,
    playerAction: null,
  });

  useEffect(() => {
    if (inGame) {
      gameViewDispatch({ type: 'gameStart' });
    } else {
      gameViewDispatch({ type: 'gameEnd' });
    }
  }, [inGame]);

  const gameStateString = JSON.stringify(gameViewState.gameState?.to_js_value() ?? null);
  useEffect(() => {
    const gameStateJson = JSON.parse(gameStateString);
    if (!gameStateJson) {
      return;
    }
    const gameState = GameState.from_js_value(gameStateJson);
    if (isEndgame(gameState)) {
      dispatchToast(createEndgameToast(gameState), { timeout: -1, intent: 'success' });
      onGameEnd();
      return;
    }
    const playerActionId = maxPlayerActionId++;
    gameViewDispatch({ type: 'playerActionStart', playerActionId });
    const playerFunction = gameState.current_player === Player.Black ? blackPlayer : whitePlayer;
    playerFunction(gameState, playerActionId).then((playerAction) => {
      gameViewDispatch({ type: 'playerActionReady', playerActionId, playerAction });
    });
  }, [onGameEnd, blackPlayer, whitePlayer, dispatchToast, gameStateString]);

  // Dispatch an action whenever the game state changes. The action may be invalid, but the reducer
  // will safely ignore it if it is.
  useEffect(() => {
    gameViewDispatch({ type: 'playerActionApply' });
  }, [gameViewState]);

  const inAnimation = gameViewState.previousSquareStates !== null;
  useEffect(() => {
    if (inAnimation) {
      onAnimationStart();
    }
  }, [onAnimationStart, inAnimation]);

  const handleClick = useCallback((row: number, column: number) => {
    if (!resolveClickPromise.current) {
      return;
    }
    const gameStateJson = JSON.parse(gameStateString);
    if (!gameStateJson) {
      return;
    }
    const flatIndex = row * 8 + column;
    if (!getLegalMoveFlatIndices(GameState.from_js_value(gameStateJson)).includes(flatIndex)) {
      return;
    }
    resolveClickPromise.current(PlayerAction.move_to_flat_index(flatIndex));
    resolveClickPromise.current = null;
  }, [gameStateString]);

  const handleAnimationEnd = useCallback(() => {
    gameViewDispatch({ type: 'animationEnd' });
    onAnimationEnd();
  }, [onAnimationEnd]);

  return (
    <>
      <Toaster toasterId={toasterId} />
      <Board
        squareStates={getSquareStatesFromGameState(gameViewState.gameState)}
        previousSquareStates={gameViewState.previousSquareStates}
        onClick={handleClick}
        onAnimationEnd={handleAnimationEnd}
      />
    </>
  );
}

/**
 * Properties of the GameView component.
 */
export type GameViewProps = {
  blackSettings: PlayerSettings,
  whiteSettings: PlayerSettings,
  agentWorker: Worker,
  inGame: boolean,
  onGameEnd: () => void,
  onAnimationStart: () => void,
  onAnimationEnd: () => void,
};

type ResolveClickPromise = (playerAction: PlayerAction) => void;
type ResolveAgentPromises = Record<number, (playerAction: PlayerAction) => void>;

let maxPlayerActionId = 0;

/**
 * Returns the name of the given player.
 */
function getPlayerName(player: Player) {
  switch (player) {
    case Player.Black:
      return 'Black';
    case Player.White:
      return 'White';
  }
}

/**
 * Returns a promise that resolves to the player's action. The action may be a pass or a move made
 * by a user click or an agent.
 */
function getPlayerActionPromise(
  gameState: GameState,
  playerSettings: PlayerSettings,
  playerActionId: number,
  agentWorker: Worker,
  dispatchPlayerPassToast: (content: React.ReactNode) => void,
  resolveClickPromise: React.MutableRefObject<ResolveClickPromise | null>,
  resolveAgentPromises: React.MutableRefObject<ResolveAgentPromises>,
): Promise<PlayerAction> {
  const actions = gameState.actions_vec();
  if (actions[0].is_pass()) {
    const playerName = getPlayerName(gameState.current_player);
    const opponentName = getPlayerName(player_opponent(gameState.current_player));
    dispatchPlayerPassToast(
      <Toast>
        <ToastTitle action={<ToastTrigger><Link>Dismiss</Link></ToastTrigger>}>
          {`${playerName} has passed`}
        </ToastTitle>
        <ToastBody>
          {`${playerName} ran out of moves and it is ${opponentName}'s turn.`}
        </ToastBody>
      </Toast>
    );
    return Promise.resolve(actions[0]);
  }
  if (playerSettings.type === 'human') {
    return new Promise((resolve) => {
      resolveClickPromise.current = resolve;
    });
  }
  return new Promise((resolve) => {
    resolveAgentPromises.current[playerActionId] = resolve;
    const message: AgentWorkerStartMessage = {
      type: 'start',
      gameStateString: JSON.stringify(gameState.to_js_value()),
      agentSettings: playerSettings,
      playerActionId,
    };
    agentWorker.postMessage(message);
  });
}

/**
 * Creates a toast that displays the result at the end of a game.
 */
function createEndgameToast(gameState: GameState) {
  let blackCount = 0;
  let whiteCount = 0;
  for (let flatIndex = 0; flatIndex < 8 * 8; ++flatIndex) {
    switch (gameState.get_disc_at_flat_index(flatIndex)) {
      case Player.Black:
        ++blackCount;
        break;
      case Player.White:
        ++whiteCount;
        break;
    }
  }
  let titleText: string;
  let bodyText: string;
  if (blackCount === whiteCount) {
    titleText = 'It is a draw!';
    bodyText = `Both players have ${blackCount} discs.`;
  } else {
    const [winner, winnerCount, loser, loserCount] = blackCount > whiteCount
      ? [Player.Black, blackCount, Player.White, whiteCount]
      : [Player.White, whiteCount, Player.Black, blackCount];
    const winnerName = getPlayerName(winner);
    const loserName = getPlayerName(loser);
    titleText = `${winnerName} wins!`;
    bodyText = (
      `${winnerName} wins with ${winnerCount} discs, ` +
      `beating ${loserName} with ${loserCount} disc${loserCount > 1 ? 's' : ''}.`
    );
  }
  return (
    <Toast>
      <ToastTitle action={<ToastTrigger><Link>Dismiss</Link></ToastTrigger>}>
        {titleText}
      </ToastTitle>
      <ToastBody>{bodyText}</ToastBody>
    </Toast>
  );
}
