import BoardConstants from './BoardConstants';
import getSquareProps, { SquareProps } from './getSquareProps';
import SquareState, { isDiscState, isLegalMoveState } from './SquareState';

/**
 * Returns an SVG element that defines a square transitioning between the given states.
 */
export default function createSquareDef(id: string, fromState: SquareState, toState: SquareState) {
  const elementInfo: Record<string, PropsAndChildren> = {
    shadow: {
      props: {
        filter: `url(#${BoardConstants.DISC_SHADOW_FILTER_ID})`,
      },
      children: [],
    },
    blackLegalMove: {
      props: {
        fill: 'black',
        transform: 'rotate(45)',
      },
      children: [],
    },
    // Rotate the legal move indicator by 45 degrees so it has a diamond shape.
    whiteLegalMove: {
      props: {
        fill: 'white',
        transform: 'rotate(45)',
      },
      children: [],
    },
    blackDisc: {
      props: {
        fill: `url(#${BoardConstants.BLACK_DISC_GRADIENT_ID})`,
      },
      children: [],
    },
    whiteDisc: {
      props: {
        fill: `url(#${BoardConstants.WHITE_DISC_GRADIENT_ID})`,
      },
      children: [],
    },
  };
  // If the transition is between a legal move and a disc, rotate the disc and its shadow by 45
  // degrees to align them with the legal move indicator. We do not always rotate the disc because
  // the rotation conflicts with the disc flip animation.
  if (
    (isLegalMoveState(fromState) && isDiscState(toState)) ||
    (isDiscState(fromState) && isLegalMoveState(toState))
  ) {
    for (const key of ['shadow', 'blackDisc', 'whiteDisc']) {
      elementInfo[key].props.transform = 'rotate(45)';
    }
  }
  // If the transition is a disc flip, play a scaling animation that imitates a flip.
  const isDiscFlip = isDiscState(fromState) && isDiscState(toState) && fromState !== toState;
  if (isDiscFlip) {
    for (const key of ['shadow', 'blackDisc', 'whiteDisc']) {
      elementInfo[key].children.push(
        <animateTransform
          key='transform'
          attributeName='transform'
          attributeType='XML'
          type='scale'
          dur={`${BoardConstants.ANIMATION_DURATION_IN_SECONDS}s`}
          fill='freeze'
          calcMode='spline'
          values='1 1; 0.2 1; 1 1'
          keyTimes='0; 0.5; 1'
          keySplines='0.5 0.1 0.9 0.5; 0.1 0.5 0.5 0.9'
        />
      );
    }
  }
  const fromProps = getSquareProps(fromState, toState);
  const toProps = getSquareProps(toState, fromState);
  for (const [key, info] of Object.entries(elementInfo)) {
    // Positions and sizes.
    for (const attributeName of ['x', 'y']) {
      addAttributeAsPropOrAnimation(
        info,
        attributeName,
        fromProps.size / -2,
        toProps.size / -2,
        false,
      );
    }
    for (const attributeName of ['width', 'height']) {
      addAttributeAsPropOrAnimation(info, attributeName, fromProps.size, toProps.size, false);
    }
    for (const attributeName of ['rx', 'ry']) {
      addAttributeAsPropOrAnimation(
        info,
        attributeName,
        fromProps.cornerRadius,
        toProps.cornerRadius,
        false,
      );
    }
    // Opacities.
    const literalKey = key as keyof SquareProps['opacities'];
    addAttributeAsPropOrAnimation(
      info,
      'opacity',
      fromProps.opacities[literalKey],
      toProps.opacities[literalKey],
      // If the transition is a disc flip, the disc color changes discretely.
      isDiscFlip && (key === 'blackDisc' || key === 'whiteDisc'),
    );
  }
  // Specify the keys explicitly to ensure the correct order.
  return (
    <g key={id} id={id}>
      {['shadow', 'blackLegalMove', 'whiteLegalMove', 'blackDisc', 'whiteDisc'].map((key) => {
        const literalKey = key as keyof SquareProps['opacities'];
        if (fromProps.opacities[literalKey] === 0 && toProps.opacities[literalKey] === 0) {
          // No need to render an element with zero opacity.
          return null;
        }
        const { props, children } = elementInfo[key];
        return (
          <rect key={key} {...props}>{children}</rect>
        );
      })}
    </g>
  );
}

/**
 * The properties and children of an SVG element.
 */
type PropsAndChildren = {
  props: Record<string, string>,
  children: JSX.Element[],
};

/**
 * If the given attribute does not change, adds it as a property of the element. Otherwise, adds an
 * animation of the attribute as a child element.
 */
function addAttributeAsPropOrAnimation<T extends string | number>(
  info: PropsAndChildren,
  attributeName: string,
  fromValue: T,
  toValue: T,
  discrete: boolean,
) {
  if (fromValue === toValue) {
    info.props[attributeName] = String(toValue);
    return;
  }
  const animateProps: Record<string, string> = {
    attributeName,
    dur: `${BoardConstants.ANIMATION_DURATION_IN_SECONDS}s`,
    fill: 'freeze',
    values: `${fromValue}; ${toValue}`,
  };
  if (discrete) {
    animateProps.calcMode = 'discrete';
    animateProps.keyTimes = '0; 0.5';
  } else {
    animateProps.calcMode = 'spline';
    animateProps.keyTimes = '0; 1';
    animateProps.keySplines = '0.1 0.5 0.5 0.9';
  }
  info.children.push(
    <animate key={attributeName} {...animateProps} />
  );
}
