import React, { memo, useCallback, useEffect, useRef } from 'react';

import BoardConstants from './BoardConstants';
import SquareState, { areSquareStatesEqual } from './SquareState';
import StatelessBoard from './StatelessBoard';

/**
 * A wrapper around the StatelessBoard component that controls animations and handles user clicks.
 */
const Board = memo(function Board({
  squareStates,
  previousSquareStates,
  onClick,
  onAnimationEnd,
}: BoardProps) {
  const inAnimation = previousSquareStates !== null;
  const ref = useRef<SVGSVGElement>(null);

  useEffect(() => {
    if (!inAnimation) {
      return;
    }
    const timeoutId = setTimeout(
      onAnimationEnd,
      BoardConstants.ANIMATION_DURATION_IN_SECONDS * 1000,
    );
    return () => {
      clearTimeout(timeoutId);
    };
  }, [onAnimationEnd, inAnimation]);

  const handleClick = useCallback((event: React.MouseEvent<SVGSVGElement>) => {
    if (inAnimation) {
      return;
    }
    const screenCTM = ref.current!.getScreenCTM();
    if (!screenCTM) {
      console.error('Failed to get screen CTM');
      return;
    }
    const point = new DOMPoint(event.clientX, event.clientY).matrixTransform(screenCTM.inverse());
    const row = Math.floor(point.y / BoardConstants.SQUARE_SIZE);
    const column = Math.floor(point.x / BoardConstants.SQUARE_SIZE);
    if (0 <= row && row < 8 && 0 <= column && column < 8) {
      onClick(row, column);
    }
  }, [onClick, inAnimation]);

  return (
    <StatelessBoard
      key={JSON.stringify(squareStates)}
      ref={ref}
      fromSquareStates={previousSquareStates ?? squareStates}
      toSquareStates={squareStates}
      onClick={handleClick}
    />
  );
}, arePropsEqual);

export default Board;

/**
 * Properties of the Board component.
 */
export type BoardProps = {
  squareStates: SquareState[][],
  previousSquareStates: SquareState[][] | null,
  onClick: (row: number, column: number) => void,
  onAnimationEnd: () => void,
};

/**
 * Returns whether the two sets of properties are equal.
 */
function arePropsEqual(prevProps: BoardProps, nextProps: BoardProps) {
  return (
    areSquareStatesEqual(prevProps.squareStates, nextProps.squareStates) &&
    areSquareStatesEqual(prevProps.previousSquareStates, nextProps.previousSquareStates) &&
    prevProps.onClick === nextProps.onClick &&
    prevProps.onAnimationEnd === nextProps.onAnimationEnd
  );
}
