import { GameState, Player } from "othello-rs";

import SquareState from "./SquareState";

/**
 * Returns whether the given game state is an endgame.
 *
 * In othello-rs, the endgame is the state where both players have passed their turns. This
 * definition is not suitable for interactive games, so we look one step ahead to determine if the
 * game should end.
 */
export function isEndgame(gameState: GameState) {
  if (gameState.is_endgame) {
    return true;
  }
  const actions = gameState.actions_vec();
  return actions[0].is_pass() && gameState.apply_action(actions[0]).is_endgame;
}

/**
 * Returns an array of square states from the given game state, or an empty board if the game state
 * is null.
 */
export function getSquareStatesFromGameState(gameState: GameState | null) {
  const squareStates = Array.from<undefined, SquareState[]>(
    { length: 8 },
    () => Array(8).fill(SquareState.Empty),
  );
  if (!gameState) {
    return squareStates;
  }
  const legalMoveFlatIndices = getLegalMoveFlatIndices(gameState);
  const legalMoveState = gameState.current_player === Player.Black ?
    SquareState.BlackLegalMove : SquareState.WhiteLegalMove;
  for (let row = 0; row < 8; ++row) {
    for (let column = 0; column < 8; ++column) {
      const flatIndex = row * 8 + column;
      if (legalMoveFlatIndices.includes(flatIndex)) {
        squareStates[row][column] = legalMoveState;
      } else {
        switch (gameState.get_disc_at_flat_index(flatIndex)) {
          case Player.Black:
            squareStates[row][column] = SquareState.BlackDisc;
            break;
          case Player.White:
            squareStates[row][column] = SquareState.WhiteDisc;
            break;
        }
      }
    }
  }
  return squareStates;
}

/**
 * Returns the flat indices of the legal moves in the given game state.
 */
export function getLegalMoveFlatIndices(gameState: GameState) {
  const indices = [];
  for (const action of gameState.actions_vec()) {
    if (!action.is_pass()) {
      indices.push(action.flat_index());
    }
  }
  return indices;
}
